@use '../util/colors.scss';

#footer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;

    padding: 0px 20px 0px 20px;
    background-color: colors.$dark;
    box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.6);

    h4 {
        margin: 0px 10px 0px;
    }

    @media screen and (max-width: 700px) {
        flex-direction: column;
        
        h4 {
            margin: 2px 0px 0px;
        }
    }
}